// File icons
import { removeBeforeSlash } from "@rp/common/utils"
import {
  CsvIcon,
  DefaultExtIcon,
  DocIcon,
  EmlIcon,
  HtmlIcon,
  PdfIcon,
  PptIcon,
  TxtIcon,
  XlsIcon,
  XmlIcon,
} from "@rp/react/common/assets"
import { SvgIcon } from "@rp/react/web/mui/core"

type Props = {
  extension?: string
}

const FileIcon = ({ extension }: Props) => {
  const cleanExtension = removeBeforeSlash(extension)
  switch (cleanExtension) {
    case "pdf":
      return <SvgIcon data-testid="pdf-svg" component={PdfIcon} />
    case "doc":
    case "docx":
      return <SvgIcon data-testid="doc-svg" component={DocIcon} />
    case "csv":
      return <SvgIcon data-testid="csv-svg" component={CsvIcon} />
    case "xls":
    case "xlsx":
      return <SvgIcon data-testid="xls-svg" component={XlsIcon} />
    case "ppt":
      return <SvgIcon data-testid="ppt-svg" component={PptIcon} />
    case "htm":
    case "html":
      return <SvgIcon data-testid="html-svg" component={HtmlIcon} />
    case "txt":
      return <SvgIcon data-testid="txt-svg" component={TxtIcon} />
    case "xml":
      return <SvgIcon data-testid="xml-svg" component={XmlIcon} />
    case "eml":
      return <SvgIcon data-testid="eml-svg" component={EmlIcon} />
    // From contentType
    case "msword":
      return <SvgIcon data-testid="doc-svg" component={DocIcon} />
    case "vnd.ms-excel":
      return <SvgIcon data-testid="xls-svg" component={XlsIcon} />
    case "vnd.ms-powerpoint":
      return <SvgIcon data-testid="ppt-svg" component={PptIcon} />
    case "plain":
      return <SvgIcon data-testid="txt-svg" component={TxtIcon} />
    case "x-emlx":
      return <SvgIcon data-testid="eml-svg" component={EmlIcon} />
    default:
      return <SvgIcon data-testid="unknown-svg" component={DefaultExtIcon} />
  }
}

export { FileIcon }
